.App-bg {
    background: url(./booksAppPhotos/books-bookstore-book-reading-159711.jpeg) no-repeat fixed;
    background-size: 100%;
    width: 100%;
    height: 100%;
    opacity: 0.9;
}

.App {
    display: -ms-flexbox;
    display: flex;
}

.books-list {
    padding-right: 15px;
    padding-left: 15px;
    bottom: 10px;
    width: 50%;
    left: 5%;
    color: white;
    font-size: 1.1em;
    text-align: center;
    vertical-align: center;
}

.grid-item:hover {
    cursor: pointer;
    background: blueviolet;
}

.grid-item {
    width: 50%;
    background: darkcyan;
    background-blend-mode: inherit;
    vertical-align: middle;
    padding: 20px;
    margin: 20px;
    text-align: center;
    border: white solid 2px;
    border-radius: 15px;
}
.infoHead{
    text-align: center;
}
.bookInfoList{
    margin-left: 5%;
}

.single-book {
    position: fixed;
    left: 50%;
    top: 15%;
    background-color: aqua;
    color: white;
    width: 30%;
    height: 20%;
    border-radius: 30px;
}

.footer {

    text-align: center;
    /*// position: "fixed";*/
    color: #F8F8F8;
    text-shadow: 3px 3px 8px black;
    left: 0;
    bottom: 0;
    height: 50px;
    width: 100%;
}

